/* eslint-disable */ 
import logo from './logo.svg';
import './App.css';
import { useState } from 'react';
import cx from "classnames";
import classNames from 'classnames';
import classname from 'classname';

function App() {
  let docnotion = "버튼을 눌러주세요";
  const [isToggled, setIstoggled] = useState(false);
  const [BW, setBW] = useState(['App1', 'App2']);
  const [sBW, setSBW] = useState(['startBW1', 'startBW2']);
  const [nBW, setNBW] = useState(['notBW1', 'notBW2']);

  function useEffect1() {
    let temp1 = 0;
    const newBW = [...BW];
    temp1 = newBW[0];
    newBW[0] = newBW[1];
    newBW[1] = temp1;
    setBW(newBW);
    return(BW);
  }
  function useEffect2() {
    let temp2 = 0;
    const newsBW = [...sBW];
    temp2 = newsBW[0];
    newsBW[0] = newsBW[1];
    newsBW[1] = temp2;
    setSBW(newsBW);
    return(sBW);
  } 
  function useEffect3() {
    let temp3 = 0;
    const newnBW = [...nBW];
    temp3 = newnBW[0];
    newnBW[0] = newnBW[1];
    newnBW[1] = temp3;
    setNBW(newnBW);
    return(nBW);
  } 

  return (
    <div className={BW[0]}>
      <div className={sBW[0]}>
        <h1 className={nBW[0]}>{docnotion}</h1>
      </div>
      <br></br>
      <ST rounded = {true} isToggled={isToggled} onToggle={() => {
        setIstoggled(!isToggled); useEffect1(); useEffect2(); useEffect3(); 
        }}></ST>
      <BT></BT>
    </div>
  );
}
function ST({rounded = false, isToggled, onToggle}) {
  const sliderCX = cx('slider',{
    'rounded':rounded
  })
  return(
    <label className='switch'>
      <input type='checkbox' checked={isToggled} onChange={onToggle}></input>
      <span className={sliderCX}></span>
    </label>
  )
}

function BT(lBW) {
  const [btnum,setBtnum] = useState(['b1','b2','b3','b4','b5','b6','b7','b8','b9']);
  function ButtonToggle(indnum) {
    const newBtnum = [...btnum];
    if(indnum === 0){
      if(btnum[indnum] === 'b1') newBtnum[indnum] = 'b1_c';
      else if(btnum[indnum] === 'b1_c') newBtnum[indnum] = 'b1';
      setBtnum(newBtnum);
    }
    else if(indnum === 1){
      if(btnum[indnum] === 'b2') newBtnum[indnum] = 'b2_c';
      else if(btnum[indnum] === 'b2_c') newBtnum[indnum] = 'b2';
      setBtnum(newBtnum);
    }
    else if(indnum === 2){
      if(btnum[indnum] === 'b3') newBtnum[indnum] = 'b3_c';
      else if(btnum[indnum] === 'b3_c') newBtnum[indnum] = 'b3';
      setBtnum(newBtnum);
    }
    else if(indnum === 3){
      if(btnum[indnum] === 'b4') newBtnum[indnum] = 'b4_c';
      else if(btnum[indnum] === 'b4_c') newBtnum[indnum] = 'b4';
      setBtnum(newBtnum);
    }
    else if(indnum === 4){
      if(btnum[indnum] === 'b5') newBtnum[indnum] = 'b5_c';
      else if(btnum[indnum] === 'b5_c') newBtnum[indnum] = 'b5';
      setBtnum(newBtnum);
    }
    else if(indnum === 5){
      if(btnum[indnum] === 'b6') newBtnum[indnum] = 'b6_c';
      else if(btnum[indnum] === 'b6_c') newBtnum[indnum] = 'b6';
      setBtnum(newBtnum);
    }
    else if(indnum === 6){
      if(btnum[indnum] === 'b7') newBtnum[indnum] = 'b7_c';
      else if(btnum[indnum] === 'b7_c') newBtnum[indnum] = 'b7';
      setBtnum(newBtnum);
    }
    else if(indnum === 7){
      if(btnum[indnum] === 'b8') newBtnum[indnum] = 'b8_c';
      else if(btnum[indnum] === 'b8_c') newBtnum[indnum] = 'b8';
      setBtnum(newBtnum);
    }
    else if(indnum === 8){
      if(btnum[indnum] === 'b9') newBtnum[indnum] = 'b9_c';
      else if(btnum[indnum] === 'b9_c') newBtnum[indnum] = 'b9';
      setBtnum(newBtnum);
    }

      
  }
  return(
    <div className="tb">
        <table>
          <tbody>
            <tr>
              <td><button id={btnum[0]} onClick={() => ButtonToggle(0)}>1</button></td>
              <td><button id={btnum[1]} onClick={() => ButtonToggle(1)}>2</button></td>
              <td><button id={btnum[2]} onClick={() => ButtonToggle(2)}>3</button></td>
            </tr>
            <tr>
              <td><button id={btnum[3]} onClick={() => ButtonToggle(3)}>4</button></td>
              <td><button id={btnum[4]} onClick={() => ButtonToggle(4)}>5</button></td>
              <td><button id={btnum[5]} onClick={() => ButtonToggle(5)}>6</button></td>
           </tr>
            <tr>
              <td><button id={btnum[6]} onClick={() => ButtonToggle(6)}>7</button></td>
              <td><button id={btnum[7]} onClick={() => ButtonToggle(7)}>8</button></td>
              <td><button id={btnum[8]} onClick={() => ButtonToggle(8)}>9</button></td>
            </tr>
          </tbody>
          
        </table>
      </div>
  );
}





export default App;
